router.beforeEach((to,from,next) => {
if (to.matched.some(record => record.meta.auth)) {

if (!store.getters.IS_AUTHENTICATION  || !store.state.UserRoleId) {
next({
path: '/login'
})
} else {
  const permission =  store.state.UserRoleId
if (to.matched.some(record => record.meta.permission)) {
if (permission === to.meta.permission) {
next();
} else {
next('/');
}
} else {
next();
}
}
} else {
next();
const logins = true
if (to.matched.some(record => record.meta.logins)){
if (store.getters.IS_AUTHENTICATION && logins === to.meta.logins) {
next('/');
}else{
next()
}
}else{
next()
}
}
})

export default router;



